import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController,
  ModalController
} from "ionic-angular";
import { AuditTableDetailViewModel } from "../../Model/ViewModel/AuditTableDetailViewModel";

/**
 * Generated class for the AuditTableDetailModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page audit-table-detail-add-edit-modal",
  templateUrl: "audit-table-detail-add-edit-modal.html"
})
export class AuditTableDetailAddEditModalPage {
  item: AuditTableDetailViewModel;
  mode: string;
  title: string;
  CanEditTable: boolean;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public modalCtrl: ModalController
  ) {
    this.item = new AuditTableDetailViewModel();
    this.item.schemaname = navParams.data.item.schemaname;
    this.item.tablename = navParams.data.item.tablename;
    this.item.columnname = navParams.data.item.columnname;
    this.item.creator = navParams.data.item.creator;
    this.item.create_time = navParams.data.item.create_time;
    this.item.modifier = navParams.data.item.modifier;
    this.item.last_update_time = navParams.data.item.last_update_time;

    this.mode = navParams.data.mode;
    this.CanEditTable = navParams.data.CanEditTable;
    if (this.mode === "POST") this.title = "新增";
    else this.title = "更新";
  }

  SelectColumn() {
    let modal = this.modalCtrl.create("TableDetailExtSelectModalPage", {
      schemaname: this.item.schemaname,
      tablename:this.item.tablename,
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.columnname = select_data.columnname;
    });

    modal.present();
  }
  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }

  ionViewDidLoad() {
    console.log("ionViewDidLoad AuditTableDetailAddEditModalPage");
  }
}



